import React from "react";
import "./pollution.css";

//ReactStrap
import { Card, CardBody, CardTitle } from "reactstrap";

const Pollutions = ({ data }) => {
    data = data.list[0];

    function AQI(value) {
        switch (value) {
            case 1:
                return <p className=" pollutonTop ">Good</p>;
            case 2:
                return <p className=" pollutonTop ">Fair</p>;
            case 3:
                return <p className=" pollutonTop ">Moderate</p>;
            case 4:
                return <p className=" pollutonTop ">Poor</p>;
            case 5:
                return <p className=" pollutonTop ">Very Poor</p>;

            default:
                break;
        }
    }

    const poll = [
        "pollution weatherBox good pb-0",
        "pollution weatherBox fair pb-0",
        "pollution weatherBox moderate pb-0",
        "pollution weatherBox poor pb-0",
        "pollution weatherBox vpoor pb-0",
    ];

    return (
        <Card className="mt-3 mb-3 text-white weatherBox pb-0">
            <CardBody className={poll[data.main.aqi - 1]}>
                <div className="row mb-2">
                    <span className="text-start mb-0 pb-0">AQI</span>
                    <CardTitle className="pt-0 mt-0">{AQI(data.main.aqi)}</CardTitle>
                </div>

                <div className="row pe-2">
                    <div className="col-md-12 p-0">
                        <p className="ms-2">
                            <span className="parameter-row mb-2">
                                <span className="parameter-label top ">
                                    Pollutants
                                </span>
                                <span className="parameter-label top ">
                                    µg/m³
                                </span>
                            </span>
                            <span className="parameter-row">
                                <span className="parameter-label">
                                    Carbon Monoxide (CO)
                                </span>
                                <span className="parameter-value">
                                    {Math.round(data.components.co)}
                                </span>
                            </span>
                            <span className="parameter-row">
                                <span className="parameter-label">
                                    Ammonia (NH₃)
                                </span>
                                <span className="parameter-value">
                                    {data.components.nh3}
                                </span>
                            </span>
                            <span className="parameter-row">
                                <span className="parameter-label">
                                    Nitrogen Monoxide (NO)
                                </span>
                                <span className="parameter-value">
                                    {Math.round(data.components.no)}
                                </span>
                            </span>
                            <span className="parameter-row">
                                <span className="parameter-label">
                                    Nitrogen Dioxide (NO₂)
                                </span>
                                <span className="parameter-value">
                                    {data.components.no2}
                                </span>
                            </span>
                            <span className="parameter-row">
                                <span className="parameter-label">
                                    Ozone (O₃)
                                </span>
                                <span className="parameter-value">
                                    {data.components.o3}
                                </span>
                            </span>
                            <span className="parameter-row">
                                <span className="parameter-label">PM₂.₅</span>
                                <span className="parameter-value">
                                    {data.components.pm2_5}
                                </span>
                            </span>
                            <span className="parameter-row">
                                <span className="parameter-label">PM₁₀</span>
                                <span className="parameter-value">
                                    {data.components.pm10}
                                </span>
                            </span>
                            <span className="parameter-row">
                                <span className="parameter-label">
                                    Sulfur dioxide (SO₂)
                                </span>
                                <span className="parameter-value">
                                    {data.components.so2}
                                </span>
                            </span>
                        </p>
                    </div>
                </div>
            </CardBody>
        </Card>
    );
};

export default Pollutions;
