import React from "react";

import { Card, CardBody, CardSubtitle, CardTitle } from "reactstrap";
import "./currentWeather.css";
import {
    Accordion,
    AccordionItem,
    AccordionItemButton,
    AccordionItemHeading,
    AccordionItemPanel,
} from "react-accessible-accordion";

//error 1  in log due to div and p in this components

const CurrentWeather = ({ data }) => {
    //
    //timestamp
    function timeStamp(currTime, timezone, value) {
        timezone = 19800 - timezone;
        var unixTimestamp = currTime - timezone;
        var date = new Date(unixTimestamp * 1000);
        switch (value) {
            case 1:
                return date.toLocaleDateString("en-GB");
            case 2:
                return date.toLocaleTimeString("it-IT");
            default:
                break;
        }
    }
    return (
        <Card className="mt-3 mb-3 bgcolor text-white weatherBox p-2 pb-0 pt-0">
            <CardBody className="">
                <div className="row mb-2">
                    <div className="col-sm-7 ps-2 pe-0 text-start">
                        <CardTitle tag={"h5"} className="fw-bold city">
                            {data.city}
                        </CardTitle>
                        <CardSubtitle tag={"h6"} className="mt-4 weather-des">
                            {data.weather[0].description}
                        </CardSubtitle>
                    </div>
                    <div className="col-sm-5 ps-0">
                        <img
                            className="weather-icon"
                            src={require(`../../Images/icons/${data.weather[0].icon}.png`)}
                            alt="weather"
                        />
                    </div>
                </div>

                <div className="row">
                    <div className="col-md-6 p-0">
                        <p className="temperature">
                            {Math.round(data.main.temp)}℃
                        </p>
                    </div>
                    <div className="col-md-6 p-0">
                        <p className="ms-2">
                            <span className="parameter-row justify-content-evenly">
                                <span className="parameter-label top mb-2">
                                    Details
                                </span>
                            </span>
                            <span className="parameter-row">
                                <span className="parameter-label">
                                    Feels like
                                </span>
                                <span className="parameter-value">
                                    {Math.round(data.main.feels_like)} ℃
                                </span>
                            </span>
                            <span className="parameter-row">
                                <span className="parameter-label">Wind</span>
                                <span className="parameter-value">
                                    {(data.wind.speed * 3.6).toFixed(1)} Km/hr
                                </span>
                            </span>
                            <span className="parameter-row">
                                <span className="parameter-label">
                                    Humidity
                                </span>
                                <span className="parameter-value">
                                    {Math.round(data.main.humidity)}%
                                </span>
                            </span>
                            <span className="parameter-row">
                                <span className="parameter-label">
                                    Pressure
                                </span>
                                <span className="parameter-value">
                                    {data.main.pressure} hPa
                                </span>
                            </span>
                        </p>
                    </div>
                </div>
                <div className="bottom-cw">
                    <Accordion allowZeroExpanded>
                        <AccordionItem>
                            <AccordionItemHeading>
                                <AccordionItemButton>
                                    <label className="ms-2 text-decoration-underline fw-lighter text-secondary mb-2">
                                        More Info
                                    </label>
                                </AccordionItemButton>
                            </AccordionItemHeading>
                            <AccordionItemPanel>
                                <div>
                                    <div className="details py-0">
                                        <span className="parameter-row">
                                            <span className="parameter-label">
                                                Date
                                            </span>
                                            <span className="parameter-value">
                                                {timeStamp(
                                                    data.dt,
                                                    data.timezone,
                                                    1
                                                )}
                                            </span>
                                        </span>
                                        <span className="parameter-row  h-25">
                                            <span className="parameter-label">
                                                Sunrise Time
                                            </span>
                                            <span className="parameter-value">
                                                {timeStamp(
                                                    data.sys.sunrise,
                                                    data.timezone,
                                                    2
                                                )}
                                            </span>
                                        </span>
                                        <span className="parameter-row">
                                            <span className="parameter-label">
                                                Sunset Time
                                            </span>
                                            <span className="parameter-value">
                                                {timeStamp(
                                                    data.sys.sunset,
                                                    data.timezone,
                                                    2
                                                )}
                                            </span>
                                        </span>
                                        <span className="parameter-row">
                                            <span className="parameter-label">
                                                Cloud
                                            </span>
                                            <span className="parameter-value">
                                                {data.clouds.all} %
                                            </span>
                                        </span>
                                        <span className="parameter-row">
                                            <span className="parameter-label">
                                                Max Temperature
                                            </span>
                                            <span className="parameter-value">
                                                {Math.round(data.main.temp_max)}{" "}
                                                ℃
                                            </span>
                                        </span>
                                        <span className="parameter-row">
                                            <span className="parameter-label">
                                                Min Temperature
                                            </span>
                                            <span className="parameter-value">
                                                {Math.round(data.main.temp_min)}{" "}
                                                ℃
                                            </span>
                                        </span>
                                        <span className="parameter-row">
                                            <span className="parameter-label">
                                                Visibility
                                            </span>
                                            <span className="parameter-value">
                                                {data.visibility / 1000} Km
                                            </span>
                                        </span>
                                    </div>
                                </div>
                            </AccordionItemPanel>
                        </AccordionItem>
                    </Accordion>
                </div>
            </CardBody>
        </Card>
    );
};

export default CurrentWeather;
